import { SHUVI_ERROR_CODE } from '@shuvi/shared/lib/constants';
import { RESET_ERROR, UPDATE_ERROR } from './pageError/actions';
export function getErrorHandler(appStore) {
    return {
        errorHandler(errorCode, errorDesc) {
            const payload = {};
            if (typeof errorCode === 'number') {
                payload.errorCode = errorCode;
                payload.errorDesc = errorDesc;
            }
            else {
                payload.errorCode = SHUVI_ERROR_CODE.APP_ERROR;
                payload.errorDesc = errorCode;
            }
            appStore.dispatch({
                type: UPDATE_ERROR,
                payload
            });
        },
        reset() {
            appStore.dispatch({
                type: RESET_ERROR
            });
        }
    };
}
