/// <reference lib="dom" />
import { CLIENT_APPDATA_ID } from '@shuvi/shared/lib/constants';
let appData = null;
export function getAppData() {
    if (appData) {
        return appData;
    }
    const el = document.getElementById(CLIENT_APPDATA_ID);
    if (!el || !el.textContent) {
        return {
            ssr: false,
            pageData: {}
        };
    }
    return JSON.parse(el.textContent);
}
