import qs from 'query-string';
import { inBrowser } from './dom';
export const trimTrailingSlashes = (path) => path.replace(/\/+$/, '');
export const normalizeSlashes = (path) => path.replace(/\/\/+/g, '/');
export const joinPaths = (paths) => normalizeSlashes(paths.join('/'));
export const splitPath = (path) => normalizeSlashes(path).split('/');
export function normalizeBase(base) {
    if (!base) {
        if (inBrowser) {
            // respect <base> tag
            const baseEl = document.querySelector('base');
            base = (baseEl && baseEl.getAttribute('href')) || '/';
            // strip full URL origin
            base = base.replace(/^https?:\/\/[^\/]+/, '');
        }
        else {
            base = '/';
        }
    }
    // make sure there's the starting slash
    if (base.charAt(0) !== '/') {
        base = '/' + base;
    }
    // remove trailing slash
    return base.replace(/\/$/, '');
}
export function parseQuery(queryStr) {
    return qs.parse(queryStr);
}
export function pathToString({ pathname = '/', search = '', hash = '', query = {} }) {
    if (!search) {
        const queryString = qs.stringify(query);
        search = queryString ? `?${queryString}` : '';
    }
    return pathname + search + hash;
}
function resolvePathname(toPathname, fromPathname) {
    let segments = splitPath(trimTrailingSlashes(fromPathname));
    let relativeSegments = splitPath(toPathname);
    relativeSegments.forEach(segment => {
        if (segment === '..') {
            // Keep the root "" segment so the pathname starts at /
            if (segments.length > 1)
                segments.pop();
        }
        else if (segment !== '.') {
            segments.push(segment);
        }
    });
    return segments.length > 1 ? joinPaths(segments) : '/';
}
/**
 * Parses a string URL path into its separate pathname, search, and hash components.
 */
export function resolvePath(to, fromPathname = '/') {
    let parsedPath = {
        pathname: '',
        search: '',
        hash: '',
        query: {}
    };
    if (typeof to === 'string') {
        if (to) {
            let hashIndex = to.indexOf('#');
            if (hashIndex >= 0) {
                parsedPath.hash = to.substr(hashIndex);
                to = to.substr(0, hashIndex);
            }
            let searchIndex = to.indexOf('?');
            if (searchIndex >= 0) {
                parsedPath.search = to.substr(searchIndex);
                parsedPath.query = parseQuery(parsedPath.search);
                to = to.substr(0, searchIndex);
            }
            if (to) {
                parsedPath.pathname = to;
            }
        }
    }
    else {
        const path = to;
        ['pathname', 'search', 'hash', 'query'].forEach(key => {
            const val = path[key];
            if (val != null) {
                // @ts-ignore
                parsedPath[key] = val;
            }
        });
        if (parsedPath.search) {
            parsedPath.query = parseQuery(parsedPath.search);
        }
        else {
            parsedPath.search = qs.stringify(parsedPath.query);
        }
    }
    const toPathname = parsedPath.pathname;
    parsedPath.pathname = toPathname
        ? resolvePathname(toPathname, toPathname.startsWith('/') ? '/' : fromPathname)
        : fromPathname;
    return parsedPath;
}
