import { RESET_ERROR, UPDATE_ERROR } from './actions';
const DEFAULTERRORSTATE = {
    errorCode: undefined,
    errorDesc: undefined
};
const error = (state = DEFAULTERRORSTATE, action) => {
    switch (action.type) {
        case RESET_ERROR:
            return Object.assign({}, DEFAULTERRORSTATE);
        case UPDATE_ERROR:
            return Object.assign(Object.assign({}, state), (action.payload ? action.payload : {}));
        default:
            return state;
    }
};
export default error;
